import { Component, Input, Renderer2, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { IFormControlComponent, IFormFocusable } from '@helix/platform/shared/api';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { ITextareaFormControlOptions } from './textarea-form-control-options.interface';
import { AdaptRxTextareaComponent } from '@bmc-ux/adapt-angular';

@Component({
  selector: 'rx-textarea-form-control',
  templateUrl: './textarea-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: TextareaFormControlComponent,
      multi: true
    }
  ],
  styleUrls: ['./textarea-form-control.component.scss']
})
export class TextareaFormControlComponent
  extends ValueAccessor<string>
  implements IFormControlComponent, IFormFocusable
{
  @Input()
  options: ITextareaFormControlOptions;

  @ViewChild('editor', { static: true })
  editor: AdaptRxTextareaComponent;

  constructor(private renderer: Renderer2) {
    super();
  }

  focus() {
    this.renderer.selectRootElement(this.editor.textareaRef.nativeElement, true).focus();
  }
}
